	module.exports = {"data":{"markdownRemark":{"html":"<blockquote class=\"large\">I'll be operating from the assumption that everything that happens in cyberspace is part of the same reality that our physical bodies occupy (sounds obvious, doesn't it?), and that a sustainable application of technology is one which increases our understanding of reality and moves us away from a passive consumption of virtuality to an active engagement with the Real.<span class=\"attribution\">Ideant.typepad.com\n(Ulises Ali Mejias)</span></blockquote>\n<p>This is such a fantastic blog that I had to point it out.  His description of our role (as consumers) in a technological world (online) is just refreshing. I've quoted a bit here from his <a href=\"http://ideant.typepad.com/ideant/2003/08/index.html\">first month of posting</a>. I went back and found his first post, and I'm trying to digest all three years of his writing. At least it is Sunday.</p>","frontmatter":{"created_at":"January 07, 2007","path":"/2007/01/07/great-blog","title":"Great Blog","tags":["blog","reading","theory","engagement"]}}},"pathContext":{}}

